:not(pre) > code {
  font-size: 1.3rem;
  font-family: "Fira Mono", monospace;
  background: #f6f8fa;
  border: 0.1rem solid #ebeced;
  padding: 0.1em 0.05em;
  margin: 0 0.1em;
}

@media screen and (min-width: 480px) {
  :not(pre) > code {
    font-size: 1.5rem;
  }
}

:not(pre) > code:before, :not(pre) > code:after {
  content: '\00a0';
  letter-spacing: -0.2em;
}

pre {
  font-size: 1.3rem;
  font-family: "Fira Mono", monospace;
  background: #f6f8fa;
  border: 0.1rem solid #ebeced;
  padding: 1em;
  margin: 1em 0;
  overflow: scroll;
}

@media screen and (min-width: 480px) {
  pre {
    font-size: 1.5rem;
  }
}

pre > code {
  color: #32414d;
  font-family: "Fira Mono", monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  tab-size: 4;
  hyphens: none;
}

.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.doctype,
.token.macro.property,
.token.punctuation,
.token.variable {
  color: #32414d;
}

.token.blockquote,
.token.cdata,
.token.comment {
  color: #6b6e70;
}

.token.builtin,
.token.important,
.token.interpolation-punctuation,
.token.keyword,
.token.operator,
.token.rule {
  color: #cf1736;
}

.token.attr-value,
.token.regex,
.token.string,
.token.url {
  color: #032f62;
}

.token.atrule,
.token.boolean,
.token.code,
.token.command,
.token.constant,
.token.entity,
.token.number,
.token.property,
.token.symbol {
  color: #005cc5;
}

.token.prolog,
.token.selector,
.token.tag {
  color: #1f7a26;
}

.token.attr-name,
.token.class,
.token.class-name,
.token.function,
.token.id,
.token.namespace,
.token.pseudo-class,
.token.pseudo-element,
.token.url-reference .token.variable {
  color: #6c37cd;
}

.token.entity {
  cursor: help;
}

.token.title,
.token.title .token.punctuation {
  font-weight: bold;
  color: #032f62;
}

.token.list {
  color: #ed6a43;
}

.token.inserted {
  background-color: #eaffea;
  color: #1f7a26;
}

.token.deleted {
  background-color: #ffecec;
  color: #cf1736;
}

::selection {
  background: #ffdd59;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  color: #32414d;
  font-family: "Fira Sans", sans-serif;
  line-height: 1.5;
}

@media screen and (min-width: 480px) {
  body {
    font-size: 1.8rem;
  }
}

h1 {
  font-size: 4rem;
  color: #000;
  line-height: 1.25;
  margin: 0.5em 0;
}

@media screen and (min-width: 480px) {
  h1 {
    font-size: 4.5rem;
  }
}

h2 {
  font-size: 2.2rem;
  color: #000;
  line-height: 1.25;
  margin: 2em 0 0.5em;
}

@media screen and (min-width: 480px) {
  h2 {
    font-size: 2.4rem;
  }
}

h3 {
  font-size: 1.6rem;
  color: #000;
  line-height: 1.25;
  margin: 2em 0 0.5em;
}

@media screen and (min-width: 480px) {
  h3 {
    font-size: 1.8rem;
  }
}

a {
  color: #3867d6;
  text-decoration: underline rgba(56, 103, 214, 0.5);
}

a:hover {
  text-decoration: underline #3867d6;
}

em,
i {
  font-style: italic;
}

ul,
ol {
  padding: 0 0 0 2em;
}

blockquote {
  color: #5b6771;
  border-left: 0.2em solid #3867d6;
  margin: 2em 0;
  padding: 0 2em;
}

table {
  font-size: 1.3rem;
  border-spacing: 0;
  border: 0.1rem solid #ebeced;
  width: 100%;
}

@media screen and (min-width: 480px) {
  table {
    font-size: 1.5rem;
  }
}

tr th,
tr td {
  text-align: left;
  vertical-align: top;
  padding: 0.5em 1em;
  border-bottom: 0.1rem solid #ebeced;
}

tr th {
  background: #f6f8fa;
  font-weight: normal;
}

tr:last-child td {
  border-bottom: none;
}

img {
  height: auto;
  max-width: 100%;
}

hr {
  border: 0;
  height: 0;
  border-bottom: 0.1rem solid #ebeced;
}

.header {
  width: 100%;
  max-width: 64rem;
  padding: 0 2rem;
  margin: 0 auto;
  display: flex;
  align-items: center;
  height: 10rem;
}

.header__logo a,
.header__logo a:hover {
  display: block;
}

.header__logo svg {
  display: block;
}

.main {
  width: 100%;
  max-width: 64rem;
  padding: 0 2rem;
  margin: 0 auto;
  padding-top: 2rem;
  padding-bottom: 6rem;
}

.hero {
  max-width: 42rem;
}

.hero__blurb {
  margin-bottom: 1.25em;
}

.hero__links a {
  font-size: 1.6rem;
  color: #32414d;
  text-decoration: none;
  border-bottom: solid 0.2rem rgba(56, 103, 214, 0.5);
}

@media screen and (min-width: 480px) {
  .hero__links a {
    font-size: 1.8rem;
  }
}

.hero__links a:hover {
  text-decoration: none;
  border-bottom: solid 0.2rem #3867d6;
}

.hero__divider {
  margin: 0 0.1em;
}

.posts a {
  font-size: 1.6rem;
  color: #32414d;
  text-decoration: none;
  border-bottom: solid 0.2rem rgba(56, 103, 214, 0.5);
}

@media screen and (min-width: 480px) {
  .posts a {
    font-size: 1.8rem;
  }
}

.posts a:hover {
  text-decoration: none;
  border-bottom: solid 0.2rem #3867d6;
}

.posts__date {
  font-size: 1.3rem;
  color: #5b6771;
  margin-left: 0.4em;
}

@media screen and (min-width: 480px) {
  .posts__date {
    font-size: 1.5rem;
  }
}

.post__date {
  display: block;
  color: #5b6771;
  margin: 2em 0 1em;
}

.footer {
  color: #bbb8b4;
  background: #1a253c;
}

.footer a {
  color: #6b8ee1;
  text-decoration: underline rgba(107, 142, 225, 0.5);
}

.footer a:hover {
  text-decoration: underline #6b8ee1;
}

.footer__inner {
  width: 100%;
  max-width: 64rem;
  padding: 0 2rem;
  margin: 0 auto;
  padding-top: 3em;
  padding-bottom: 3em;
}

.footer__blurb, .footer__copyright, .footer__links {
  margin: 1em 0;
}

.footer__divider {
  margin: 0 0.1em;
}
